.mod-social-icons{
    display: inline-block;
    text-align: center;   
}

.mod-social-icons a:hover{
    text-decoration: none;
}   

.mod-social-icons a i{ 
    background-color: white; 
    background-position: center center;
}

.mod-social-icons.sm{
    font-size: 16px!important;
    padding: 0;   
}

.mod-social-icons.sm a{
    font-size: 1em!important; 
    margin-right: 0.15em;
}

.mod-social-icons.sm a:last-child{
    margin-right: 0em;
}

.mod-social-icons.sm a span{
    font-size: 1em!important;
} 

.mod-social-icons.sm a i{ 
    border: 1px solid #cecece;
    font-size: 1em!important; 
    line-height: 24px!important; 
    border-radius: 6px;
    height: 28px;
    width: 28px;    
    padding: 1px 2px;
}

.mod-social-icons.lg{
    font-size: 14px!important;
    padding: 0;   
}

.mod-social-icons.lg a{
    font-size: 1em!important; 
    margin-right: 0.5em;
}

.mod-social-icons.lg a:last-child{
    margin-right: 0em;
}

.mod-social-icons.sm a span{
    font-size: 1em!important;
} 

.mod-social-icons.lg a i{ 
    font-size: 1em!important; 
    line-height: 32px!important; 
    border-radius: 6px;
    height: 38px;
    width: 38px;    
    padding-top: 2px;
}




